.App {
  text-align: center;
  align-items: center;
  justify-content: center;
}
.btns {
  margin-top: 8px;
  margin-left: 10px;
  background-color: lightgreen;
  color: black;
  font-size: 19px;
  transition: all 0.5s ease-out;
  cursor: pointer;
  border: 2px solid;
  border-radius: 20px;
  border-color: lightpink;
}
.btns:hover{
  color: white;
  background-color: lightslategray;
}
